import React from 'react'
import './Home.css'
import GitHubIcon from '@mui/icons-material/GitHub';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import EmailIcon from '@mui/icons-material/Email';
import { Tooltip } from '@mui/material';
import { IconButton } from '@mui/material';

function Home() {
    return (
        <div className="home">
            <div className="home__mobile">
                <h1 className="accent__1">Hello <span className="emoji">👋</span></h1>
                <h1 className="accent__1">I'm <span className="name accent__2">Tharn</span></h1>
                <p>Software Developer</p>
                <hr />
                <div className="social">
                    <IconButton>
                        <a href="mailto:tharun@tuta.io" target="_blank" rel="noopener noreferrer">
                            <EmailIcon/>
                        </a>
                    </IconButton>
                    <IconButton>
                        <a href="https://github.com/tharunbirla" target="_blank" rel="noopener noreferrer">                
                            <GitHubIcon/>
                        </a>
                    </IconButton>
                    <IconButton >
                        <a href="https://www.instagram.com/tharunbirla/" target="_blank" rel="noopener noreferrer">
                            <InstagramIcon/>
                        </a>
                    </IconButton>
                    <IconButton>
                        <a href="https://twitter.com/tharunbirla/" target="_blank" rel="noopener noreferrer">
                            <TwitterIcon/>
                        </a>
                    </IconButton>
                </div>
            </div>
            <div className="home__desktop">
                <h1 className="accent__1">Hello <span className="emoji">👋</span></h1>
                <h1 className="accent__1">I'm <span className="name accent__2">Tharn</span></h1>
                <p>Software Developer</p>
                <hr />
                <div className="social">
                    <Tooltip title="tharun@tuta.io" arrow>
                        <a href="mailto:tharun@tuta.io" target="_blank" rel="noopener noreferrer">
                            <EmailIcon/>
                        </a>
                    </Tooltip>
                    <Tooltip title="@tharunbirla" arrow>                
                        <a href="https://github.com/tharunbirla" target="_blank" rel="noopener noreferrer">                
                            <GitHubIcon/>
                        </a>
                    </Tooltip>
                    <Tooltip title="@tharunbirla" arrow>
                        <a href="https://www.instagram.com/tharunbirla/" target="_blank" rel="noopener noreferrer">
                            <InstagramIcon/>
                        </a>
                    </Tooltip>
                    <Tooltip title="@tharunbirla" arrow>
                        <a href="https://twitter.com/tharunbirla/" target="_blank" rel="noopener noreferrer">
                            <TwitterIcon/>
                        </a>
                    </Tooltip>
                </div>
            </div>
        </div> 
    );
}

export default Home;