import React, { useState, useEffect } from 'react'
import './Repositories.css'
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';


export default function Repositories() {
const [Users, fetchUsers] = useState([])

const getData = () => {
    fetch('https://api.github.com/users/tharunbirla/repos')
    .then((res) => res.json())
    .then((res) => {
        console.log(res)
        fetchUsers(res)
})
}

useEffect(() => {
    getData()
}, [])


return (
    <div className="repos">
        {Users.map((item) => {
        return (
            <div className="repo">
                <Card sx={{ maxWidth: 345 }} className="repo_container">
                <CardContent className="repo_cards accent__1">
                    <Typography gutterBottom variant="h5" className="repo_name" component="div">
                    {item.name}
                    </Typography>
                    <Typography variant="body2" className="repo_description" color="text.secondary">
                    {item.description}
                    </Typography>
                    <Typography className="repo_description">
                    {item.language}
                    </Typography>
                </CardContent>
                <CardActions>
                    <Button className="accent__2" size="small"><a href={item.html_url} target="_blank">Learn More</a></Button>
                </CardActions>
                </Card>
            </div>
        )
        })}
    </div>
    )
}