import './Navigation.css'
import React from 'react';
import { IconButton } from '@mui/material';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import PersonOutlineIcon from '@mui/icons-material/PersonOutline';
import CodeOutlinedIcon from '@mui/icons-material/CodeOutlined';
import AlternateEmailOutlinedIcon from '@mui/icons-material/AlternateEmailOutlined';
import { Link } from "react-router-dom";

function Navigation() {
    return ( 
        <div className="nav">
            <div className="nav__desktop">
            <Link to="/">
            <IconButton><HomeOutlinedIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            <Link to="/about">
            <IconButton><PersonOutlineIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            <Link to="/projects">
            <IconButton><CodeOutlinedIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            <Link to="/contact">
            <IconButton><AlternateEmailOutlinedIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            </div>
            <div className="nav__mobile">
            <Link to="/">
            <IconButton><HomeOutlinedIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            <Link to="/about">
            <IconButton><PersonOutlineIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            <Link to="/projects">
            <IconButton><CodeOutlinedIcon className="icons" fontSize="large"/></IconButton>
            </Link>
            <Link to="/contact">
            <IconButton><AlternateEmailOutlinedIcon className="icons" fontSize="large"/></IconButton>
            </Link>
        </div>
        </div>
    );
}

export default Navigation;